import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    logined: sessionStorage.getItem('log'),
    reader: window.speechSynthesis,
  },
  mutations: {
    changelog(state) {
      sessionStorage.setItem('log', true)
      state.logined = true
    }
  },
  actions: {
  },
  modules: {
  }
})
