<template>
  <div class="intro-mental">
    <div class="video-mental">
      <p class="subtitle">
        Use our website to get a recommendation of books based on your specific interests and preferences!
      </p>
      <video src="../../../assets/mental.mp4" muted loop autoplay></video>
      <div class="search-mental">
        <div style="margin-top: 15px">
          <!-- if genre is select, autocomplete will be open -->
          <el-input
            v-if="select != 1"
            placeholder="Search..."
            v-model="bookterm"
            class="input-with-select"
            @keydown.native.enter="searchbtn"
          >
            <el-select v-model="select" slot="prepend" placeholder="Select">
              <el-option label="Genre" value="1"></el-option>
              <el-option label="Author" value="2"></el-option>
              <el-option label="Publisher" value="3"></el-option>
            </el-select>
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchbtn"
            ></el-button>
          </el-input>
          <el-autocomplete
            v-else
            class="inline-input"
            v-model="bookterm"
            :fetch-suggestions="querySearch"
            placeholder="Search..."
            @keydown.native.enter="searchbtn"
          >
            <el-select v-model="select" slot="prepend" placeholder="Select">
              <el-option label="Genre" value="1"></el-option>
              <el-option label="Author" value="2"></el-option>
              <el-option label="Publisher" value="3"></el-option>
            </el-select>
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchbtn"
            ></el-button>
          </el-autocomplete>
        </div>
      </div>
    </div>
    <section class="book-mental">
      <div v-if="!searched" class="guess-mental">
        <guess></guess>
      </div>
      <div v-else class="searchResult">
        <sr :searchterm="genreterm"></sr>
      </div>
    </section>
  </div>
</template>

<script>
import guess from '@/components/mental/guess'
import sr from '@/components/mental/search'

export default {
  data() {
    return {
      bookterm: "",
      select: "1",
      searched: false,
      genreterm: {},
      genre: [{value: 'Fantasy'}, {value: 'History'}, {value: 'Cooking'}, {value: 'Health'}, {value: 'Mystery'}, {value: 'Humor'}, {value: 'Romance'}, {value: 'Travel'}, {value: 'Fiction'}, {value: 'Art'}],
    };
  },
  components: {
    'guess': guess,
    'sr': sr,
  },
  methods: {
    // change the input type when user select genre
    querySearch(queryString, cb) {
      let genre = this.genre;
      let results = queryString ? genre.filter(this.createFilter(queryString)) : genre;
      cb(results);
    },
    // filter the genre when user typing
    createFilter(queryString) {
      return (genre) => {
        return (genre.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    // click event.
    searchbtn () {
      this.genreterm = {
        type: this.select,
        term: this.bookterm
      }
      this.searched = true
    },
    // backtop() {
    //   let timer = null;
    //   clearInterval(timer);
    //   // create intervel 15ms
    //   timer = setInterval(()=>{
    //     scrollTop -= scrollTop / 10;
    //     window.scrollTo(0,scrollTop)
    //     // when scrolltop < 2, clearInterval
    //     if(scrollTop<2){
    //       window.scrollTo(0,0);
    //       clearInterval(timer);
    //     }
    //   },15)
    // }
  },

};
</script>
<style>
.subtitle {
  padding: 30px;
  border-radius: 15px;
  /* border: 1px solid #6c25a9; */
  position: absolute;
  left: 80px;
  top: 100px;
  width: 500px;
  color: #fff;
  font-size: 28px;
  font-family: Arial, Helvetica, sans-serif;
}
.intro-mental {
  position: relative;
  max-width: 1300px;
  min-width: 1000px;
  width: 100%;
  height: 100%;
  left: 50%;
  transform: translate(-50%);
  overflow: hidden;
}
.video-mental {
  position: relative;
  width: 100%;
  height: 710px;
  margin-bottom: 30px;
}
.video-mental video {
  width: 100%;
  height: 100%;
}
.search-mental {
  position: absolute;
  width: 700px;
  height: 60px;
  left: 50%;
  top: 50%;
  transform: translate(-50%);
}
.search-mental .el-select .el-input {
  width: 100px;
}
.book-mental {
  width: 80%;
  margin: 0 auto;
}
.guess-mental {
  width: 100%;
}
.searchResult {
  width: 100%;
}
.inline-input {
  width: 100%;
}
.bookmore {
  height: 48px;
  font-size: 18px;
  line-height: 48px;
  border: 1px solid #444444;
  padding: 0 20px;
  display: block;
  margin: 10px auto 20px auto;
  width: 150px;
  color: black;
  text-align: center;
  opacity: 0.7;
  text-decoration: none;
  background-color: #af74e1;
}
.bookmore:hover {
  opacity: 1;
}
</style>