<template>
  <div class="transfer">
    <div class="cateBox">
      <div class="h3box">
        <h3 class="title">Food Category</h3>
      </div>
      <input
        type="text"
        name="focus"
        required
        v-model="term"
        class="search-box"
        placeholder="Search..."
      />
      <div class="cg disabled" ref="leftbox">
        <el-checkbox-group v-model="checkList" @change="curtick">
          <el-checkbox
            v-for="item in curList"
            :label="item"
            :key="item"
          ></el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
    <div class="detail">
      <div class="h3box">
        <h3 class="title" style="color: #fb4839">Food Item</h3>
      </div>
      <input
        type="text"
        name="focus"
        required
        v-model="foodterm"
        class="search-box2"
        placeholder="Search..."
      />
      <div class="cg disabled" ref="rightbox">
        <el-table
          style="width: 97%"
          :data="curfood"
          height="215"
          :header-cell-style="{color:'#333',fontFamily:'MicrosoftYaHeiUI',fontSize:'14px',fontWeight:900}"
          :stripe="true"
          :row-style="{ height: 0 + 'px' }"
          :cell-style="{ padding: 0 + 'px' }"
        >
          <el-table-column
            prop="Food Name"
            label="Food Name"
            width="320"
          ></el-table-column>
          <el-table-column label="Quantity(*100g)" width="145">
            <template slot-scope="scope">
              <el-input-number
                v-model="scope.row.quantity"
                @change="((currentValue, oldValue) => {changeNum(currentValue, oldValue, scope.row)})"
                :min="0"
                size="mini"
              ></el-input-number>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategory, getmeal } from "@/network/api";

export default {
  async created() {
    this.categoryList = await getCategory();
    console.log(this.categoryList)
    this.curList = this.categoryList;
  },
  props:['value'],
  data() {
    return {
      curList: [], // category list show in website
      categoryList: [], // real list for the category list
      checkList: [],
      term: "",
      foodterm: "",
      mock: [], // real list for the food term list
      curfood: [],
      num4: 0,
      inlist: [],
    };
  },
  watch: {
    value(newVal) {
      if(newVal == true){
        this.$refs.leftbox.classList.remove('disabled')
        this.$refs.rightbox.classList.remove('disabled')
      }
    },
    // listening term, the logic of the search function is the item must start with the search term
    term(newVal, oldVal) {
      let len = newVal.length;
      if (newVal !== oldVal && len !== 0) {
        // this.curList = this.categoryList.filter(key => key.toLowerCase().indexOf(newVal) !== -1)
        // filter by serch term (start with OK!)
        this.curList = this.categoryList.filter(
          (key) => key.toLowerCase().substring(0, len) === newVal
        );
      } else if (len == 0) {
        this.curList = this.categoryList;
      }
    },
    // listening food term, the logic of the search function is the item must include the search term
    foodterm(newVal, oldVal) {
      let len = newVal.length;
      if (newVal !== oldVal && len !== 0) {
        // console.log(newVal)
        // filter by serch term (including OK!)
        this.curfood = this.mock.filter(key => key['Food Name'].toLowerCase().indexOf(newVal) !== -1)
        
      } else if (len == 0) {
        this.curfood = this.mock;
      }
    },
  },
  methods: {
    // when user tick the food category, send request to get the result of what is in such category.
    async curtick(val) {
      this.$emit('step2')
      let query = [];
      if (val.length < 1) {
        // clear everything
        this.mock = [];
        this.curfood = [];
        this.inlist = [];
        return;
      }
      // when user canceled some category
      if (val.length < this.inlist.length) {
        // console.log("OK");
        query = val.concat(this.inlist);
        query = query.filter(
          (item) => query.indexOf(item) == query.lastIndexOf(item)
        );
        this.inlist.splice(this.inlist.indexOf(query[0]), 1);
        this.mock = this.mock.filter(
          (item) => item["Food Category"] !== query[0]
        );
        this.curfood = this.mock
        return;
      }
      // only add category, avoid send same request twice
      if (val.length > this.inlist.length) {
        // combine two array to find out which one the user add
        query = val.concat(this.inlist);
        query = query.filter(
          (item) => query.indexOf(item) == query.lastIndexOf(item)
        );
        this.inlist.push(query[0]);
      }

      // convert array to the string.
      query = query.join(",");
      // send request to get food list
      let meal = await getmeal(query);
      meal = meal.data;
      // meal.forEach((arr) => {
      //   let cate = arr[0]["Food Category"];
      //   // this.inlist.push(cate);
      //   if (!localStorage.getItem(cate)) {
      //     localStorage.setItem(cate, JSON.stringify(arr));
      //   }
      // });
      meal = meal.flat();
      this.mock = this.mock.concat(meal);
      this.curfood = this.mock
    },
    changeNum(currentValue, oldValue, row) {
      // console.log(currentValue, oldValue, row)
      if(currentValue < oldValue) {
        this.$emit('update', row, 'neg')
      }else {
        this.$emit('update', row, 'pos')
      }
      // console.log(row);
      // this.$emit('update', row)
    },
  },
};
</script>
<style scoped>
.transfer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: linear 0.3s;
}
.cateBox {
  width: 170px;
  height: 300px;
  border-radius: 7px;
  box-shadow: 0 0 5px 0 blueviolet;
  /* transition: linear 0.3s; */
  margin-left: 50px;
  overflow: hidden;
}
.detail {
  width: 500px;
  height: 300px;
  border-radius: 7px;
  margin-left: 50px;
  /* transition: linear 0.3s; */
  box-shadow: 0 0 5px 0 #fb4839;
  overflow: hidden;
  /* background-color: lightgray; */
}

.h3box {
  width: 100%;
  height: auto;
  box-shadow: 0 1px 0 0 #4444;
}
.title {
  text-align: center;
  color: blueviolet;
  padding: 5px;
}
.cg {
  width: 100%;
  margin: 10px;
  height: auto;
}
.el-checkbox {
  margin: 5px;
  display: block;
}
.el-checkbox-group {
  width: 100%;
  height: 215px;
  overflow: auto;
}
.cg .el-table th {
  padding: 0;
}

/* searchbox css */
.search-box,
.close-icon,
.search-wrapper {
  position: relative;
  padding: 10px;
  left: 50%;
  transform: translate(-50%);
  margin-top: 5px;
}
.search-wrapper {
  width: 100%;
  margin: auto;
}
.search-box {
  width: 80%;
  height: 5px;
  border: 1px solid #ccc;
  outline: 0;
  border-radius: 15px;
}
.search-box:focus {
  box-shadow: 0 0 5px 0px blueviolet;
  /* border: 2px solid #bebede; */
}

.search-box2,
.search-wrapper {
  position: relative;
  padding: 10px;
  left: 50%;
  transform: translate(-50%);
  margin-top: 5px;
}
.search-wrapper {
  width: 100%;
  margin: auto;
}
.search-box2 {
  width: 80%;
  height: 5px;
  border: 1px solid #ccc;
  outline: 0;
  border-radius: 15px;
}
.search-box2:focus {
  box-shadow: 0 0 5px 0px #fb4839;
  /* border: 2px solid #bebede; */
}
.disabled {
    pointer-events: none;
    cursor: not-allowed;
    opacity: 0.3;
}
/* searchbox css end */
</style>


