<template>
  <div class="wrapper">
    <div class="logbox">
      <h3>LogIn</h3>
      <el-input
        v-model="account"
        placeholder="username"
        class="account"
        auto-complete="new-password"
      ></el-input>
      <el-input
        v-model="password"
        placeholder="password"
        show-password
        class="password"
        auto-complete="new-password"
      ></el-input>
      <el-button
        type="primary"
        class="login"
        @click="btnlogin"
        >Login</el-button
      >
    </div>
  </div>
</template>

<script>
import crypto from 'crypto'

export default {
  data() {
    return {
      password: "",
      account: "",
    }
  },
  created() {
  },
  methods: {
    btnlogin() {
      let md5 = crypto.createHash("md5")
      md5.update(this.password)
      this.password = md5.digest('hex')
      if(this.password == '695bce6a7a898877c5cc69d0d704031e' && this.account == 'agedcare') {
        this.$store.commit('changelog')
        this.$router.replace('/home')
      }else {
        this.$message.error('account/password error')
      }
    }
  }
};
</script>
<style scoped>
.wrapper {
  width: 100%;
  height: 100%;
  /* background: #eef0f2; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.logbox {
  position: relative;
  width: 400px;
  height: 460px;
  background: #ffffff;
  box-shadow: 0px 0px 25px rgba(40, 120, 255, 0.38);
  opacity: 1;
  border-radius: 10px;
}

.account {
  width: 75%;
  position: absolute;
  left: 50%;
  top: 35%;
  transform: translate(-50%);
}

.password {
  width: 75%;
  position: absolute;
  left: 50%;
  top: 48%;
  transform: translate(-50%);
}
.login {
  position: absolute;
  left: 50%;
  top: 67%;
  transform: translate(-50%);
  width: 75%;
}
h3 {
  position: absolute;
  left: 50%;
  top: 12%;
  transform: translate(-50%);
}
</style>