<template>
  <div>
    <div class="recomd">
      <div class="title">
        <div class="side"></div>
        <div class="rightside">
          <div class="subject">Result: {{searchterm.term}}</div>
          <div class="refresh" @click="more">
            <svg t="1618543763622" class="icon" viewBox="0 0 1257 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2644" width="20" height="20"><path d="M745.47264 1024c11.264 0 23.552-4.096 31.744-13.312L1244.16064 543.744a45.12 45.12 0 0 0 0-63.488L777.21664 13.312a45.12 45.12 0 0 0-63.488 0c-18.432 17.408-18.432 46.08 0 64.512L1147.90464 512 713.72864 946.176c-18.432 18.432-18.432 47.104 0 64.512 8.192 9.216 20.48 13.312 31.744 13.312zM45.05664 557.056h1102.848A45.44 45.44 0 0 0 1192.96064 512a44.8 44.8 0 0 0-45.056-45.056H45.05664A44.8 44.8 0 0 0 0.00064 512c0 24.576 20.48 45.056 45.056 45.056z" p-id="2645" fill="#1296db"></path></svg>
            <p>Next Page</p>
          </div>
          <div class="free">
            <el-switch v-model="value" active-text="Free" inactive-text="">
            </el-switch>
          </div>
        </div>
      </div>
      <div class="list">
        <div class="card" v-for="item in temp" :key="item.id">
          <bookcard :book="item"></bookcard>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSearch, searchAuthor, searchPublisher } from "@/network/api.js";
import bookcard from "@/components/mental/book";

export default {
  props: ['searchterm'],
  created() {
    if(this.searchterm.type == 1) {
      this.getSubject()
    }else if (this.searchterm.type == 2) {
      this.getAuthor()
    }
  },
  data() {
    return {
      result: {
        index: 0,
        result: "",
      },
      temp: "",
      value: false,
      
    }
  },
  methods: {
    async getPublisher() {
      try {
        let result = await searchPublisher(this.searchterm.term, this.result.index);
        if (result == 'no result') {
          this.open4('No Result');
          return
        }
        if (result !== "no data") {
          this.result.index = result.pop() + 1;
          this.result.result = result;
          if(!this.value) {
            this.temp = result;
          }else {
            this.temp = result.filter(item => item.sale !== "FOR_SALE")
          }
        } else {
          // if there is no more book behind the index, rest the index to 0
          this.result.index = 0;
          this.getSubject(this.searchterm.term);
        }
      } catch (error) {
        this.open4(error);
      }
    },
    async getAuthor() {
      try {
        let result = await searchAuthor(this.searchterm.term, this.result.index);
        if (result == 'no result') {
          this.open4('No Result');
          return
        }
        if (result !== "no data") {
          this.result.index = result.pop() + 1;
          this.result.result = result;
          if(!this.value) {
            this.temp = result;
          }else {
            this.temp = result.filter(item => item.sale !== "FOR_SALE")
          }
        } else {
          // if there is no more book behind the index, rest the index to 0
          this.result.index = 0;
          this.getSubject(this.searchterm.term);
        }
      } catch (error) {
        this.open4(error);
      }
    },
    async getSubject() {
      try {
        let result = await getSearch(this.searchterm.term, this.result.index);
        if (result == 'no result') {
          this.open4('No Result');
          return
        }
        if (result !== "no data") {
          this.result.index = result.pop() + 1;
          this.result.result = result;
          if(!this.value) {
            this.temp = result;
          }else {
            this.temp = result.filter(item => item.sale !== "FOR_SALE")
          }
        } else {
          // if there is no more book behind the index, rest the index to 0
          this.result.index = 0;
          this.getSubject(this.searchterm.term);
        }
      } catch (error) {
        this.open4(error);
      }
    },
    more() {
      if(this.searchterm.type == 1) {
          this.getSubject()
        }else if(this.searchterm.type == 2) {
          this.getAuthor()
        }else if(this.searchterm.type == 3) {
          this.getPublisher()
        }
    },
    // error function 
    open4(str) {
      this.$notify.error({
        title: "Error",
        message: str,
        offset: 150,
        position: "top-left",
      });
    },
  },
  watch: {
    // listen the searchterm value, if it changes, do some logic
    searchterm(newVal, oldVal) {
      if(newVal.type !== oldVal.type) {
        this.result = {
          index: 0,
          result: "",
        }
      }
      if(newVal.term !== '') {
        if(newVal.type == 1) {
          this.getSubject()
        }else if(newVal.type == 2) {
          this.getAuthor()
        }else if(newVal.type == 3) {
          this.getPublisher()
        }
      }
    },
    value(newVal, oldVal) {
      if(newVal) {
        this.temp = this.result.result.filter(item => item.sale !== "FOR_SALE")
      }else {
        this.temp = this.result.result
      }
    }
  },
  components: {
    bookcard: bookcard,
  }
}

</script>
<style scoped>
.recomd {
  width: 100%;
  height: 1200px;
  margin-bottom: 60px;
}
.title {
  width: 100%;
  height: 40px;
  /* background-color: bisque; */
  display: flex;
  margin-bottom: 10px;
}
.title .side {
  width: 5px;
  height: 25px;
  background-color: coral;
  margin: 7px 10px 5px 10px;
  border-radius: 10px;
}
.title .rightside {
  width: 95%;
}
.title .subject {
  float: left;
  font-size: 22px;
  margin: 7px 0 0 10px;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
}
.title .refresh {
  float: right;
  font-size: 17px;
  margin: 10px 10px 0 10px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.refresh svg {
  margin-right: 5px;
}
.refresh:hover {
  color: coral;
}
.recomd .list {
  width: 100%;
  height: 430px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: space-between;
}
.recomd .card {
  width: 20%;
  height: 50%;
}
.free {
  float: right;
  margin: 10px 40px 0 10px;
}
</style>