<script>
import "swiper/swiper-bundle.min.css";
import Swiper, { EffectCoverflow } from "swiper";
import { getlink } from "@/network/api";
import bmi from "@/components/exc/bmi";
import condition from "@/components/exc/condition";
import str from "@/components/exc/strbafl";

Swiper.use([EffectCoverflow]);

export default {
  data() {
    return {
      comp: "",
      type: "",
      result: "",
    };
  },
  mounted() {
    let swiper = new Swiper(".first", {
      effect: "coverflow",
      grabCursor: true,
      centeredSlides: true,
      slidesPerView: "auto",
      coverflowEffect: {
        rotate: 30,
        stretch: 0,
        depth: 500,
        modifier: 1,
        slideShadows: true,
      },
    });
  },
  components: {
    bmi: bmi,
    condition: condition,
    str: str,
  },
  methods: {
    async pass(type) {
      let result = await getlink(type);
      console.log(this.result);
      this.type = type;
      this.result = result;
      this.dsh = true;
      // console.log(type)
    },
  },
};
</script>
