import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Enter from '../views/Enter.vue'
import store from '@/store/index.js'
import Meal from '../views/home/meal.vue'
import exc from '../views/home/exc.vue'
import book from '../views/home/mental/book.vue'

Vue.use(VueRouter)

const routes = [
{
  path: '/',
  name: 'Enter',
  component: Enter
},
{
  path: '/home',
  name: 'Home',
  component: Home,
  children: [{
    path: '/',
    name: 'website',
    component: () => import(/* webpackChunkName: "website" */ '../views/home/websit.vue')
  }, {
    path: '/home/vis',
    name: 'vis',
    component: () => import(/* webpackChunkName: "vis" */ '../views/home/vis.vue')
  }, {
    path: '/home/ins',
    name: 'ins',
    component: () => import(/* webpackChunkName: "ins" */ '../views/home/ins.vue')
  }, {
    path: '/home/meal',
    name: 'meal',
    component: Meal,
  }, {
    path: '/home/exc',
    name: 'exc',
    component: exc,
  }, {
    path: '/home/mental',
    name: 'mental',
    component: () => import(/* webpackChunkName: "mental" */ '../views/home/mental.vue')
  }, {
    path: '/home/book',
    name: 'Book',
    component: book,
  }, {
    path: '/home/music',
    name: 'Music',
    component: () => import(/* webpackChunkName: "music" */ '../views/home/mental/music.vue'),
  }
  // {
  //   path: '/home/model',
  //   name: 'model',
  //   component: () => import(/* webpackChunkName: "model" */ '../views/home/model.vue')
  // }, {
  //   path: '/home/future',
  //   name: 'future',
  //   component: () => import(/* webpackChunkName: "mental" */ '../views/home/future.vue')
  // }, {
  //   path: '/home/profile',
  //   name: 'profile',
  //   component: () => import(/* webpackChunkName: "mental" */ '../views/home/profile.vue')
  // }
]
},
{
  path: '/c',
  name: 'Caregiver',
  component: () => import(/* webpackChunkName: "caregiver" */ '../views/caregiver.vue')
},
  {
    path: '/footer',
    name: 'Footer',
    component: () => import(/* webpackChunkName: "footer" */ '../components/footer.vue')
  }
]

const router = new VueRouter({
  scrollBehavior(to, from, saveTop) {
    if (saveTop) {
      return saveTop;
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes
})

// if user did not log in, will redirect to he enter page
router.beforeEach((to, from, next) => {
  if (to.name !== 'Enter' && !store.state.logined) next({ name: 'Enter' })
  if (to.name == 'Enter' && store.state.logined) next({ name: 'Home' })
  next()
})

export default router
