<template>
  <div class="bmi">
      <div class="tit">
        <p>
          BMI for older adults(aged 65+) is different compared to that of adults
          and young population
        </p>
      </div>
      <div class="table">
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="category" label="Category">
          </el-table-column>
          <el-table-column prop="adults" label="BMI Range for Adults">
          </el-table-column>
          <el-table-column prop="older" label="BMI Range for Older Adults"> </el-table-column>
        </el-table>
      </div>
      <div class="tit">
        <p>
          The guidelines are generally higher for elderly. It's all too common for older adults with a BMI of 25-29.9 to be advised that they need to lose weight. However, they are already in the best BMI category for their age (with the lowest mortality risk!)
        </p>
      </div>
      <div class="bigtit">
        <p>
          But why should elderly have higher BMI?
        </p>
      </div>
      <div class="tit">
        <p>
          To start with, adults start losing muscle as they grow old. Higher BMI in the elderly is also associated with low mortality rates. Older adults already have less muscle mass. Losing weight can lead to further loss in muscle which can be fatal. Regular exercise and engaging in physical activities help retain muscles in elderly.
        </p>
      </div>
      <a href="javascript:void(0);" @click="showbmi">Know your BMI</a>
      <div class="tit" style="margin-top: 40px">
        <p>
          Along with muscles, it is also important to keep the bones strong. Strength, Flexibility and Balance exercises help avoid osteoporosis (loss of bone density) and fractures which is very common in older adults. They also help to maintain weight and avoid fat gain.
        </p>
      </div>
      <el-dialog
      title="BMI Calculator"
      :visible.sync="bmi"
      :append-to-body="true"
      width="35%"
    >
      <el-input
        placeholder="Weight(kg)"
        v-model="weight"
        type="number"
        clearable>
      </el-input>
      <el-input
        style="margin-top: 20px"
        placeholder="Height(cm)"
        v-model="height"
        type="number"
        clearable>
      </el-input>
      <el-button type="success" style="margin-top: 20px; width:100%" @click="compute">Compute</el-button>
      <p style="margin-top: 20px; font-size: 20px">Your BMI is {{bmiresult}}</p>
      <p style="color: red; margin-top: 20px; font-size: 20px" v-if="bmiresult < 23">You are underweight, consult your GP soon!</p>
    </el-dialog>
    </div>
</template>

<script>
export default {
  data() {
    return {
      bmi: false,
      weight: "",
      height: "",
      bmiresult: "?",
      tableData: [
        {
          category: "Underweight",
          adults: "< 18.5",
          older: "< 23",
        },
        {
          category: "Normal",
          adults: "18.5 - 24.9",
          older: "23 - 30",
        },
        {
          category: "Overweight",
          adults: "25 - 29.9",
          older: "> 31",
        },
        {
          category: "Obese",
          adults: "30+",
          older: "30+",
        },
      ],
    }
  },
  methods: {
    showbmi() {
      this.bmi = true
    },
    compute() {
      // validation for bmi
      if(this.height == "" || this.height < 100 || this.height > 250) {
        this.open4('Height is illegal (100cm - 250cm)')
        return
      }
      if(this.weight == "" || this.weight < 25 || this.weight > 120) {
        this.open4('Weight is illegal (25kg - 120kg)')
        return
      }
      let height = this.height / 100
      this.bmiresult = (this.weight / (height * height)).toFixed(2)
    },
    open4(str) {
      this.$notify.error({
        title: "Error",
        message: str,
        offset: 150,
        position: "top-left",
      });
    },
  },
  mounted () {
    // document.body.scrollTop = document.documentElement.scrollTop = 1050;
    $('html,body').animate({scrollTop: 1300}, "slow");
  }
}

</script>
<style scoped>
.bmi {
  /* font-family: "Comic Sans MS", "Courier New", Courier, monospace; */
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  margin: 20px auto;
  width: 80%;
  text-align: center;
}
.table {
  margin: 20px;
}
.bigtit {
  margin: 10px;
  font-size: 30px;
  text-align: left;
}
.tit {
  font-size: 17px;
}
.bmi a {
  text-align: left;
}
</style>