<template>
  <div class="detail">
    <div class="image" @click="bookdialog = true">
      <div class="rate"><strong>{{book.rate.toFixed(1)}}</strong></div>
      <!-- <img :src="book.image" alt="NO COVER AVALIABLE" width="100%" height="100%"> -->
      <div class="pic">
        <el-image :src="book.image" style="width: 100%; height: 100%">
          <div slot="placeholder" class="image-slot">
            loading<span class="dot">...</span>
          </div>
        </el-image>
      </div>
      <div class="read">
        <svg
          t="1618475654289"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="18985"
          width="48"
          height="48"
        >
          <path
            d="M924.16 56.96A581.44 581.44 0 0 0 512 219.52 581.44 581.44 0 0 0 99.84 56.96 65.92 65.92 0 0 0 32 121.28v640a66.88 66.88 0 0 0 69.44 64 579.84 579.84 0 0 1 370.56 128 64 64 0 0 0 79.68 0 579.84 579.84 0 0 1 370.56-128 66.88 66.88 0 0 0 69.44-64v-640a65.92 65.92 0 0 0-67.52-64.32zM419.52 686.4a40 40 0 0 1-54.4 15.68 465.6 465.6 0 0 0-177.6-56 40 40 0 0 1 8.96-79.36 551.68 551.68 0 0 1 207.68 64 40 40 0 0 1 15.36 55.68z m0-160a40 40 0 0 1-54.4 15.68 465.6 465.6 0 0 0-177.6-56 40 40 0 0 1 8.96-79.36 551.68 551.68 0 0 1 207.68 64 40 40 0 0 1 15.36 55.68z m0-160a40 40 0 0 1-54.4 15.68 465.6 465.6 0 0 0-177.6-56 40 40 0 0 1 8.96-79.68 551.68 551.68 0 0 1 207.68 64 40 40 0 0 1 15.36 56z m416 279.68a465.6 465.6 0 0 0-177.6 56 40 40 0 0 1-38.72-70.08 551.68 551.68 0 0 1 207.68-64 40 40 0 0 1 8.96 79.36z m0-160a465.6 465.6 0 0 0-177.6 56 40 40 0 0 1-38.72-70.08 551.68 551.68 0 0 1 207.68-64 40 40 0 0 1 8.96 79.36z m0-160a465.6 465.6 0 0 0-177.6 56 40 40 0 0 1-38.72-70.08 551.68 551.68 0 0 1 207.68-64 40 40 0 0 1 8.96 79.36z"
            fill="#d81e06"
            p-id="18986"
          ></path>
        </svg>
      </div>
    </div>
    <div class="title">
      {{ book.title }}
    </div>
    <el-dialog
      :title="book.title"
      :visible.sync="bookdialog"
      :append-to-body="true"
      width="40%"
      @close="cancel"
    >
      <div>
        <div class="diahead">
          <div class="diaimg">
            <div class="pic">
              <el-image :src="book.image" style="width: 110px; height: 150px">
                <div slot="placeholder" class="image-slot">
                  loading<span class="dot">...</span>
                </div>
              </el-image>
            </div>
          </div>
          <div class="info">
            <div><strong>Author:</strong></div>
            <p>{{ book.author.join(", ") }}</p>
            <div><strong>Page Count:</strong></div>
            <p>{{ book.page }}</p>
            <div><strong>Category:</strong></div>
            <p>{{ book.category.join(", ") }}</p>
            <div><strong>Language:</strong></div>
            <p>{{ book.language }}</p>
            <!-- <div><strong>Sale:</strong></div>
            <p>{{ book.sale }}</p> -->
          </div>
        </div>
        <div style="margin-bottom: 20px"><strong>Description:</strong></div>
        <div class="des">
          <div class="senc">{{ book.description }}</div>
          <div class="play" @click="play">
            <div v-if="!cur">
              <svg
                t="1618507714294"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="3403"
                width="64"
                height="64"
              >
                <path
                  d="M512 1024A512 512 0 1 1 512 0a512 512 0 0 1 0 1024zM383.232 287.616v448l384-223.104-384-224.896z"
                  fill="#d81e06"
                  p-id="3404"
                ></path>
              </svg>
            </div>
            <div v-else>
              <svg t="1618510441086" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3989" width="64" height="64"><path d="M512 1024A512 512 0 1 1 512 0a512 512 0 0 1 0 1024z m3.008-92.992a416 416 0 1 0 0-832 416 416 0 0 0 0 832zM320 320h128v384H320V320z m256 0h128v384H576V320z" fill="#d81e06" p-id="3990"></path></svg>
            </div>
          </div>
        </div>
        <div class="link">
          <!-- <a :href="book.previewLink" target="__blank">Preview Link</a> -->
          <a :href="book.infoLink" target="__blank">Get Book</a>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
let utterThis = null
export default {
  props: ["book"],
  data() {
    return {
      bookdialog: false,
      cur: false,
    };
  },
  methods: {
    play() {
      if (!this.cur) {
        this.$store.state.reader.resume();
        utterThis = new SpeechSynthesisUtterance(this.book.description); // init convet text to audio
        utterThis.pitch = 1; // voice level
        utterThis.rate = 1; // audio speed
        utterThis.volume = 1;
        this.cur = true
        this.$store.state.reader.speak(utterThis); // start
      }else {
        this.$store.state.reader.pause()
        this.cur = false
      }
    },
    cancel() {
      this.$store.state.reader.cancel()
    }
  },
  mounted() {
    // console.log(this.book)
  },
  beforeDestroy() {
  }
};
</script>
<style scoped>
.detail {
  width: 100%;
  height: 100%;
}
.image {
  cursor: pointer;
  width: 60%;
  height: 80%;
  overflow: hidden;
  margin: 3px auto;
  border-radius: 5px;
  position: relative;
}
.rate {
  position: absolute;
  top: 0;
  left: 0;
  /* border-radius: 5px; */
  color: white;
  background: red;
  width: 35px;
  height: 14px;
  font-size: 12px;
  text-align: center;
  z-index: 2;
}
.title {
  width: 90%;
  padding-left: 10px;
  padding-top: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 700;
  text-align: center;
}
.image .pic {
  transition: 0.3s linear;
  width: 100%;
  height: 100%;
}
.image:hover .pic {
  transform: scale(1.2);
  opacity: 0.5;
}
.read {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5 linear;
}
.image:hover .read {
  opacity: 1;
}
.diahead {
  width: 100%;
  height: 170px;
  display: flex;
  border-top: 1px solid #444444;
  border-bottom: 1px solid #444444;
  padding-top: 10px;
  margin-bottom: 5px;
}
.diaimg {
  width: 110px;
  height: 150px;
}
.info {
  margin-left: 40px;
}
.des {
  position: relative;
  width: 100%;
  border-bottom: 1px solid #9c9b9b;
  padding-bottom: 10px;
}
.play {
  position: absolute;
  width: 64px;
  height: 64px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  cursor: pointer;
  /* transition: .5s ease-in-out; */
}
.des:hover .senc {
  opacity: 0.5;
}
.des:hover .play {
  opacity: 1;
}
.link {
  margin-top: 10px;
  margin-bottom: 10px;
}
.link a {
  float: right;
  margin-right: 30px;
  text-decoration: none;
}
</style>