<template>
  <div class="c-intro">
    <div class="c-reason">
      <p>
        Our website recommends exercises based on the individual health needs of
        each user. We want to know what health conditions you may have, so that
        we can give you exercises which helps address your conditions! Select
        any of the below conditions which apply to you and we will provide a
        plan which you can use to improve and address the impacts of your
        condition.
      </p>
    </div>
    <div class="c-box">
      <el-checkbox-group v-model="checkList">
        <el-checkbox-button label="Arthiritis"></el-checkbox-button>
        <el-checkbox-button label="Joint pain"></el-checkbox-button>
        <el-checkbox-button label="Muscle loss with aging"></el-checkbox-button>
        <el-checkbox-button label="Obesity"></el-checkbox-button>
        <el-checkbox-button label="Osteoporosis"></el-checkbox-button>
      </el-checkbox-group>
      <div class="c-single">
        <el-radio-group v-model="radio1">
          <el-radio-button label="Strength"></el-radio-button>
          <el-radio-button label="Balance"></el-radio-button>
          <el-radio-button label="Flexibility"></el-radio-button>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button
          type="success"
          icon="el-icon-search"
          @click="submit"
          >search</el-button
        >
      </div>
    </div>
    <div v-if="ts" style="padding: 20px 100px;" class="ts">
      <div v-if="result.length == 0">
        <div class="back">
          <div>
            NO MATCH DATA
          </div>
        </div>
      </div>
      <el-carousel v-else height="500px" arrow="always" autoplay='false' indicator-position='none'>
        <el-carousel-item v-for="(item, index) in result" :key="index">
          <h1>{{ item["Exercise Name"] }} ({{item['Core Areas']}})</h1>
          <p>{{ item["Description"] }}</p>
          <h3 style="color: red">{{ item["Exercise Precautions"] }}</h3>
          <div class="video">
            <iframe
              :src="item['Youtube Link']"
              frameborder="0"
              allowfullscreen
              width="630"
              height="300"
            ></iframe>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>

<script>
import { getCondition } from "@/network/api";

let result;

export default {
  data() {
    return {
      checkList: ["Arthiritis",],
      radio1: "Strength",
      ts: false,
      result: "",
      activeName: 1,
    };
  },
  methods: {
    async submit() {
      result = await getCondition(this.checkList);
      this.result = result.filter(item => item['Core Areas'] == this.radio1)
      if(this.result.length == 0) {
        this.open4('No data')
      }
      this.ts = true
      
    },
    open4(str) {
      this.$notify.error({
        title: "Error",
        message: str,
        offset: 150,
        position: "top-left",
      });
    },
  },
  mounted() {
    // document.body.scrollTop = document.documentElement.scrollTop = 1050;
    $('html,body').animate({scrollTop: 1300}, "slow");
  },
  watch: {
    radio1(newVal, oldVal) {
      this.result = result.filter(item => item['Core Areas'] == this.radio1)
    },
  }
};
</script>
<style>
.c-reason {
  width: 80%;
  margin: 70px auto;
  border-radius: 5px;
  background: #333;
  box-shadow: 0 25px 35px rgba(0, 0, 0, 0.1);
  border-bottom: 6px solid #6c25a9;
  padding: 20px 10px 20px 10px;
  opacity: 0.7;
  transition: 0.3s;
  color: #ffffff;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}
.c-reason:hover {
  opacity: 1;
}
.c-box {
  width: 80%;
  margin: 30px auto;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.c-box .el-checkbox-button__inner {
  font-size: 20px;
}
.c-single {
  margin-top: 30px;
}
.c-single .el-radio-button__inner {
  font-size: 20px;
}
.c-single .el-radio-button__orig-radio {
  background-color: orange;
}
.c-box .btn {
  margin-top: 30px;
}
.c-box .el-button {
  width: 300px;
}
.c-box .el-button {
  font-size: 20px;
}

.ts h1 {
  text-align: center;
  /* margin-top: 50px; */
  /* margin-bottom: 20px; */
}
.ts p {
  text-align: center;
  width: 800px;
  margin: 0 auto;
  font-size: 17px;
}
.ts h3 {
  text-align: center;
  margin: 20px;
  font-size: 20px;
}
.ts .video {
  margin: 10px auto;
  width: 630px;
  background-color: #444444;
}
.ts .back {
  background-color: #99a9bf;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  height: 500px;
}
</style>