      </el-step>
      <el-step title="Step 3">
        <template slot="description">
          <p style="fontSize: 16px; text-align: center">
            Please select the quantity of food you are eating
          </p>
        </template>
      </el-step>
      <el-step title="Step 4">
        <template slot="description">
          <p style="fontSize: 16px; text-align: center">
            Get your recommendation!
          </p>
        </template>
      </el-step>
    </el-steps>
    <section class="relate">
      <!-- gender and age form -->
      <div class="form" ref="agegender">
        <el-form
          :model="numberValidateForm"
          ref="numberValidateForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="Gender" required>
            <el-radio-group v-model="gender" size="medium">
              <el-radio border label="Female"></el-radio>
              <el-radio border label="Male"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="Age"
            prop="age"
            :rules="[
              { required: true, message: 'Age is required' },
              { type: 'number', message: 'Number Please' },
            ]"
          >
            <el-input
              type="age"
              v-model.number="numberValidateForm.age"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              class="submit"
              type="primary"
              @click="submitForm('numberValidateForm')"
              >Show required energy intake</el-button
            >
          </el-form-item>
        </el-form>
      </div>
      <div class="cross">
        <cate
          :key="key"
          @update="updateDate"
          @step2="active = 2"
          :value="value"
        ></cate>
      </div>
    </section>
    <!-- table shows the result -->
