            <td>{{ target["Folate in ug"] }}</td>
            <td>{{ target["Potassium in mg"] }}</td>
          </tr>
        </table>
      </section>
    </div> -->

    <div class="bg">
      <h3
        class="sheading"
        style="text-align: center; margin: 40px 0 20px; color: red"
      >
        Nutrient Contents
      </h3>
      <div class="els">
        <!-- <svg t="1617752861547" class="icon reset" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4311" width="32" height="32"><path d="M940.463158 463.494737c0-237.136842-191.326316-428.463158-428.463158-428.463158S83.536842 226.357895 83.536842 463.494737c0 59.284211 10.778947 115.873684 35.031579 169.768421 5.389474 8.084211 16.168421 13.473684 24.252632 8.08421 10.778947-5.389474 13.473684-16.168421 10.778947-24.252631-21.557895-48.505263-32.336842-99.705263-32.336842-153.6C121.263158 247.915789 296.421053 72.757895 512 72.757895s390.736842 175.157895 390.736842 390.736842S727.578947 851.536842 512 851.536842c-10.778947 0-18.863158 8.084211-18.863158 18.863158v59.284211l-167.073684-134.736843 167.073684-132.042105v59.284211c0 10.778947 8.084211 18.863158 18.863158 18.863158 153.6 0 280.252632-123.957895 280.252632-280.252632 0-153.6-123.957895-280.252632-280.252632-280.252632S231.747368 309.894737 231.747368 463.494737c0 10.778947 8.084211 18.863158 18.863158 18.863158s18.863158-8.084211 18.863158-18.863158c0-132.042105 107.789474-242.526316 242.526316-242.526316s242.526316 107.789474 242.526316 242.526316c0 126.652632-97.010526 231.747368-223.663158 239.831579v-78.147369-2.694736-2.694737s0-2.694737-2.694737-2.694737v-2.694737l-2.694737-2.694737-2.694737-2.694737h-2.694736-2.694737-2.694737-2.694737s-2.694737 0-2.694737 2.694737h-2.694737l-215.578947 172.463158v2.694737h-2.694737v2.694737s0 2.694737-2.694737 2.694737v10.778947s0 2.694737 2.694737 2.694737v2.694737h2.694737l2.694737 2.694736 215.578947 172.463158s2.694737 0 2.694737 2.694737h2.694737c2.694737 0 5.389474 2.694737 8.08421 2.694737 2.694737 0 5.389474 0 8.084211-2.694737h2.694737c2.694737 0 2.694737-2.694737 5.389473-2.694737v-2.694736l2.694737-2.694737v-2.694737-83.536842c215.578947-13.473684 398.821053-199.410526 398.821053-428.463158z" fill="#d81e06" p-id="4312"></path></svg> -->
        <el-switch v-model="value1" active-text="Vegeterian" inactive-text="">
