    </div>
      <section class="foodtable disabled" ref="nurtable">
        <el-table
          :data="result"
          style="width: 100%"
          height="750"
          :stripe="true"
          :header-cell-style="{
            color: '#6c25a9',
            fontFamily: 'MicrosoftYaHeiUI',
            fontSize: '14px',
            fontWeight: 900,
            fontWeight: 'bold',
          }"
          empty-text="Please follow the instruction"
        >
          <el-table-column fixed prop="Nutrients" label="Nutrients" width="200">
          </el-table-column>
          <el-table-column
            align="center"
            fixed
            prop="Your Energy Count"
            label="Your Energy Count"
            width="200"
          >
          </el-table-column>
          <el-table-column
            align="center"
            prop="Daily Requirement"
            label="Daily Requirement"
            width="200"
          >
          <template slot-scope="scope">
              <span :class="Math.round(
                    (parseInt(scope.row['Your Energy Count']) /
                      parseInt(scope.row['Daily Requirement'])) *
                      100
                  ) > 100 ? 'green' : 'red'">{{scope.row['Daily Requirement']}}</span>
            </template>
          </el-table-column>
          <!-- prop="Recommendations" -->
          <el-table-column label="Energy Meter" align="center" width="200">
            <template slot-scope="scope">
              <el-progress
                :percentage="
                  Math.round(
                    (parseInt(scope.row['Your Energy Count']) /
                      parseInt(scope.row['Daily Requirement'])) *
                      100
                  ) > 100
                    ? 100
                    : Math.round(
                        (parseInt(scope.row['Your Energy Count']) /
                          parseInt(scope.row['Daily Requirement'])) *
                          100
                      )
                "
              ></el-progress>
            </template>
          </el-table-column>
          <el-table-column label="Recommendations" width="150" align="center">
            <template slot-scope="scope">
              <el-popover
                placement="left"
                width="400"
                trigger="click"
                @hide="hide"
              >
                <el-table :data="recommendform" height="300">
                  <el-table-column
                    property="Recommendations"
                    label="Recommendations"
                    width="360"
                  ></el-table-column>
                </el-table>
                <el-button
                  @click="recommend(scope.row)"
                  slot="reference"
                  type="text"
                  size="small"
                  >Healthier Alternatives</el-button
                >
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </section>
