</template>

<script>
import { getmeal, getTarget, getRecm } from "@/network/api.js";
import cate from "@/components/meal/cate";

let middle;
let map = {
  "Protein in g": "Protein",
  "Vitamin A in ug": "Vitamin A",
  "Vitamin C in mg": "Vitamin C",
  "Calcium in mg": "Calcium",
  "Folate in ug": "Folate (Vitamin B9)",
  "Potassium in mg": "Potassium",
  "Iron in mg": "Iron",
  "Zinc in mg": "Zinc",
  "Vitamin B6 in mg": "pyridoxine (Vitamin B6)",
  "Vitamin B12 in ug": "cyanocobalamin (Vitamin B12)",
  "Magnesium in mg": "Magnesium",
};

export default {
  data() {
    return {
      key: +new Date(),
      value1: false,
      active: 0,
      title: "",
      dialogTableVisible: false,
      recommendform: [], // recommendation result for table
      food: "",
      value: false,
      gender: "Female",
      numberValidateForm: {
        age: "",
      },
      result: [],
      target: {
        // progress bar
        // "Protein in g": 0,
        // "Vitamin A in ug": 0,
        // "Vitamin C in mg": 0,
        // "Calcium in mg": 0,
        // "Folate in ug": 0,
        // "Potassium in mg": 0,
      },
      showtarget: false,
    };
  },
  components: {
    cate: cate,
  },

  methods: {
    // send ajax request to get meal
    async send() {
      // get Recommended Daily Nutrient Allowances
      let t = await getTarget(this.numberValidateForm.age, this.gender);
      this.target = t.data[0];
      this.result = [];
      let temp = [];
      Object.keys(this.target).forEach((item) => {
        temp.push({
          Nutrients: item,
          "Your Energy Count": 0,
          "Daily Requirement": `${this.target[item]}${item
            .split(" ")
            .pop()}/day`,
          Recommendations: 0,
        });
      });
      this.result = temp;
      this.active = 1;
      this.value = true;
      // this.$refs.agegender.classList.add('disabled')
    },
    submitForm(formName) {
      this.key = +new Date();
      this.value = false;
      this.$refs.nurtable.classList.add("disabled");
      // age and gender validation rules
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.numberValidateForm.age < 60) {
            this.open4("Age must be over 59");
            return;
          } else if (this.numberValidateForm.age > 110) {
            this.open4("Age must be under 110");
            return;
          }
          this.send();
          this.open1();
        } else {
          this.open4("Age is required");
          return false;
        }
      });
    },
    open4(str) {
      this.$notify.error({
        title: "Error",
        message: str,
        offset: 150,
        position: "top-left",
      });
    },
    open1() {
      this.$notify({
        title: "Success",
        message: "Submited",
        type: "success",
      });
    },

    // data comes from child component(cate), data is the quantity of the food
    updateDate(obj, str) {
      this.$refs.nurtable.classList.remove("disabled");
      this.active = 3;
      // update nutrient one by one
      // calculate the nutrient
      // console.log(obj)
      if (str == "pos") {
        this.result.forEach((item) => {
          switch (item["Nutrients"]) {
            case "Protein in g":
              item["Your Energy Count"] =
                (
                  obj["Protein in g"] + parseInt(item["Your Energy Count"])
                ).toFixed(1) + "g/day";
              break;
            case "Vitamin A in ug":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) +
                  obj["Vitamin A retinol equivalents in ug"]
                ).toFixed(1) + "ug/day";
              break;
            case "Vitamin C in mg":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) + obj["Vitamin C in mg"]
                ).toFixed(1) + "mg/day";
              break;
            case "Folate in ug":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) +
                  obj["Folate natural in ug"]
                ).toFixed(1) + "ug/day";
              break;
            case "Calcium in mg":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) +
                  obj["Calcium (Ca) in mg"]
                ).toFixed(1) + "mg/day";
              break;
            case "Potassium in mg":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) +
                  obj["Potassium (K) in mg"]
                ).toFixed(1) + "mg/day";
              break;
            case "Zinc in mg":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) + obj["Zinc in mg"]
                ).toFixed(1) + "mg/day";
              break;
            case "Vitamin B6 in mg":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) + obj["Vitamin B6 in mg"]
                ).toFixed(1) + "mg/day";
              break;
            case "Iron in mg":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) + obj["Iron in mg"]
                ).toFixed(1) + "mg/day";
              break;
            case "Vitamin B12 in ug":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) + obj["Vitamin B12 in ug"]
                ).toFixed(1) + "ug/day";
              break;
            case "Magnesium in mg":
              item["Your Energy Count"] =
                (
                  parseInt(item["Your Energy Count"]) + obj["Magnesium in mg"]
                ).toFixed(1) + "mg/day";
              break;
          }
        });
      } else {
        this.result.forEach((item) => {
          switch (item["Nutrients"]) {
            case "Protein in g":
              let num1 = (
                parseInt(item["Your Energy Count"]) - obj["Protein in g"]
              ).toFixed(1);
              item["Your Energy Count"] = (num1 < 0 ? 0 : num1) + "g/day";
              break;
            case "Vitamin A in ug":
              let num2 = (
                parseInt(item["Your Energy Count"]) -
                obj["Vitamin A retinol equivalents in ug"]
              ).toFixed(1);
              item["Your Energy Count"] = (num2 < 0 ? 0 : num2) + "ug/day";
              break;
            case "Vitamin C in mg":
              let num3 = (
                parseInt(item["Your Energy Count"]) - obj["Vitamin C in mg"]
              ).toFixed(1);
              item["Your Energy Count"] = (num3 < 0 ? 0 : num3) + "mg/day";
              break;
            case "Folate in ug":
              let num4 = (
                parseInt(item["Your Energy Count"]) -
                obj["Folate natural in ug"]
              ).toFixed(1);
              item["Your Energy Count"] = (num4 < 0 ? 0 : num4) + "ug/day";
              break;
            case "Calcium in mg":
              let num5 = (
                parseInt(item["Your Energy Count"]) - obj["Calcium (Ca) in mg"]
              ).toFixed(1);
              item["Your Energy Count"] = (num5 < 0 ? 0 : num5) + "mg/day";
              break;
            case "Potassium in mg":
              let num6 = (
                parseInt(item["Your Energy Count"]) - obj["Potassium (K) in mg"]
              ).toFixed(1);
              item["Your Energy Count"] = (num6 < 0 ? 0 : num6) + "mg/day";
              break;
            case "Zinc in mg":
              let num7 = (
                parseInt(item["Your Energy Count"]) - obj["Zinc in mg"]
              ).toFixed(1);
              item["Your Energy Count"] = (num7 < 0 ? 0 : num7) + "mg/day";
              break;
            case "Vitamin B6 in mg":
              let num8 = (
                parseInt(item["Your Energy Count"]) - obj["Vitamin B6 in mg"]
              ).toFixed(1);
              item["Your Energy Count"] = (num8 < 0 ? 0 : num8) + "mg/day";
              break;
            case "Iron in mg":
              let num9 = (
                parseInt(item["Your Energy Count"]) - obj["Iron in mg"]
              ).toFixed(1);
              item["Your Energy Count"] = (num9 < 0 ? 0 : num9) + "mg/day";
              break;
            case "Vitamin B12 in ug":
              let numa = (
                parseInt(item["Your Energy Count"]) - obj["Vitamin B12 in ug"]
              ).toFixed(1);
              item["Your Energy Count"] = (numa < 0 ? 0 : numa) + "ug/day";
              break;
            case "Magnesium in mg":
              let numb = (
                parseInt(item["Your Energy Count"]) - obj["Magnesium in mg"]
              ).toFixed(1);
              item["Your Energy Count"] = (numb < 0 ? 0 : numb) + "mg/day";
              break;
          }
        });
      }
    },
    // show recommendation table
    async recommend(obj) {
      this.active = 4;
      let nur = map[obj["Nutrients"]];
      if (this.value1) {
        let result = await getRecm("veg", nur);
        this.recommendform = result;
      } else {
        let result = await getRecm("non", nur);
        this.recommendform = result;
      }
    },
    hide() {
      // console.log('OK')
      this.recommendform = [];
    },
  },
};
</script>
