<script>
import { getHealth, searchSubject } from "@/network/api.js";
import bookcard from "@/components/mental/book";

export default {
  data() {
    return {
      guess: {
        guessindex: 0,
        guessterm: "elderly health",
        result: "",
      },
      cooking: {
        index: 0,
        result: "",
      },
      humor: {
        index: 0,
        result: "",
      },
      history: {
        index: 0,
        result: "",
      },
      travel: {
        index: 0,
        result: "",
      },
    };
  },
  created() {
    // send query to get the books list
    this.getGuess();
    this.getSubject("cooking");
    this.getSubject("history");
    this.getSubject("travel");
    this.getSubject("humor");
  },
  components: {
    bookcard: bookcard,
  },
  methods: {
    // function for get the Guess You like, to get books list
    async getGuess() {
      try {
        let result = await getHealth(
          this.guess.guessterm,
          this.guess.guessindex
        );
        if (result !== "no data") {
          this.guess.guessindex = result.pop() + 1;
          this.guess.result = result;
        } else {
          // if there is no more book behind the index, rest the index to 0
          this.guess.guessindex = 0;
          this.getGuess();
        }
      } catch (error) {
        this.open4(error);
      }
    },
    // get books which subject is subject
    async getSubject(subject) {
      try {
        let result = await searchSubject(subject, this[subject].index);
        if (result !== "no data") {
          this[subject].index = result.pop() + 1;
          this[subject].result = result;
        } else {
          // if there is no more book behind the index, rest the index to 0
          this[subject].index = 0;
          this.getSubject(subject);
        }
      } catch (error) {
        this.open4(error);
      }
    },
// error dialog function 
    open4(str) {
      this.$notify.error({
        title: "Error",
        message: str,
        offset: 150,
        position: "top-left",
      });
    },
  },
};
</script>
