<template>
  <div id="home">
    <topnav @showlog="showlog" :scroll='scroll'></topnav>
    <div v-if="!logshow" class="mongolia" @click="showlog"></div>
    <log-box v-show="!logshow"></log-box>
    <div class="router">
      <!-- <transition enter-active-class="animate__animated animate__zoomIn" leave-active-class="animate__animated animate__zoomOut" mode="out-in"> -->
        <!-- <keep-alive> -->
          <router-view></router-view>
        <!-- </keep-alive> -->
      <!-- </transition> -->
    </div>
    <div class="footer">
      <footer-com></footer-com>
    </div>
  </div>
</template>

<script>
import topnav from "@/components/topNav.vue";
import logbox from "@/components/login.vue"; // iteration 2 function, hidden in iteration 1
import footer from '@/components/footer.vue';

export default {
  data() {
    return {
      logshow: true,
      menushow: false,
      degree: 0,
      scroll: 0,
    };
  },
  mounted() {
    // add event listener to scroll event
    window.addEventListener('scroll', this.topscroll)
  },
  methods: {
    // show login box
    showlog() {
      this.logshow = !this.logshow;
      this.menushow = false;
    },
    // scroll function
    topscroll() {
      if(window.scrollY > 0) {
        this.scroll = 1
      }else {
        this.scroll = 0
      }
    },
  },
  // register components
  components: {
    'topnav': topnav,
    "log-box": logbox,
    'footer-com': footer,
  },
};
</script>
<style scoped>
#home {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.mongolia {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.router {
  /* position: relative; */
  width: 100%;
  height: 100%;
  position: relative;
  left: 50%;
  transform: translate(-50%);
  /* top: 60px; */
  /* display: flex;
  justify-content: center;
  align-items: center; */
  /* overflow: hidden; */
  /* z-index: -1; */
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5 ease-out;
}
.footer {
  width: 100%;
  height: 250px;
}
</style>