    },
    logimg(url) {
      this.si = url;
      this.dsh = true;
    },
    btnlogin() {

    }
  },
  watch: {
    scroll() {
      if (this.scroll == 1) {
        this.$refs.top.classList.replace("topnav", "sticky");
      } else {
        this.$refs.top.classList.replace("sticky", "topnav");
      }
    },
  },
  mounted() {},
};
