  /* transition: .5s; */
}
.login ul {
  display: flex;
}
.login ul li {
  list-style: none;
}
.login ul li h4 {
  position: relative;
  display: block;
  padding: 10px 10px;
  margin: 10px 0;
  color: #ee4e70;
  /* text-decoration: none;
  text-transform: uppercase; */
  /* color: #ffffff; */
  /* font-weight: bold; */
  /* transition: .5s; */
}
.login ul li h4:hover {
  color: #ffffff;
}
.login ul li h4::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-top: 1px solid #ee4e70;
  border-bottom: 1px solid #ee4e70;
  transform: scaleY(2);
  opacity: 0;
  transition: 0.5s;
  z-index: -1;
}
.login ul li h4:hover:before {
  transform: scaleY(1.2);
  opacity: 1;
}

.login ul li h4::after {
  content: "";
  position: absolute;
  top: 1px;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ee4e70;
  transform: scale(0);
  transition: 0.5s;
  z-index: -1;
  border-radius: 6px;
}
.login ul li h4:hover:after {
  transform: scale(1);
}
