<script>
import { getlink } from "@/network/api";

export default {
  data() {
    return {
      result: "",
      vs: false,
      tableData: [
        {
          monday: "15-minute walk x 2",
          tuesday: "15-minute wald x 2",
          wednesday: "30 minute cycling, swimming, water aerobics, zumba, etc.",
          thursday: "Rest",
          friday: "30 minute wald (or 15-minute walk x 2)",
          saturday: "30 minute cycling, swimming, water aerobics, zumba, etc.",
          sunday: "Rest",
        },
        {
          monday: "Strength",
          tuesday: "",
          wednesday: "Strength",
          thursday: "",
          friday: "Strength",
          saturday: "",
          sunday: "",
        },
        {
          monday: "Balance",
          tuesday: "Balance",
          wednesday: "Balance",
          thursday: "Balance",
          friday: "Balance",
          saturday: "Balance",
          sunday: "Balance",
        },
        {
          monday: "Flexibility",
          tuesday: "Flexibility",
          wednesday: "Flexibility",
          thursday: "Flexibility",
          friday: "Flexibility",
          saturday: "Flexibility",
          sunday: "Flexibility",
        },
      ],
    };
  },
  methods: {
    async excval(item) {
      this.result = await getlink(item);
      this.vs = true;
    },
  },
  created() {},
  mounted() {
    // document.body.scrollTop = document.documentElement.scrollTop = 1050;
    $('html,body').animate({scrollTop: 1300}, "slow");
  },
};
</script>
<style scoped>
.wrapper {
  width: 100%;
  margin: 0 auto;
}
