}
.youtubelink p {
  text-align: center;
  width: 800px;
  margin: 0 auto;
  font-size: 17px;
}
.youtubelink h3 {
  text-align: center;
  margin: 20px;
  font-size: 20px;
}
.youtubelink .video {
  margin: 10px auto;
  width: 630px;
  background-color: #444444;
}

.boxlist {
  text-align: center;
  display: flex;
  margin: 20px;
}
.boxlist .box {
  flex: 1;
  /* width: 350px; */
  margin: 10px 10px;
  position: relative;
  height: 220px;
  cursor: pointer;
}
.box .back-face,
.box .front-face {
  height: 220px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0 5px 20px 0 rgba(0, 81, 250, 0.2);
  transition: all 0.4s ease;
}
.box .front-face span,
.box .back-face span {
  font-size: 22px;
  font-weight: 600;
  margin: 20px auto;
  text-transform: uppercase;
}
.box .front-face span {
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: -webkit-linear-gradient(-135deg, #c850c0, #4158d0);
}
.box .back-face {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  box-sizing: border-box;
  height: 100%;
  padding: 0 20px;
  width: 100%;
  color: #fff;
  opacity: 0;
  backface-visibility: hidden;
  background: -webkit-linear-gradient(-135deg, #c850c0, #4158d0);
  transform: translateY(110px) rotateX(-90deg);
  /* display: none; */
}
.box .back-face p {
  font-size: 20px;
}
.box:hover .back-face {
  transform: rotateX(0deg);
  opacity: 1;
}
.box:hover .front-face {
  transform: translateY(-110px) rotateX(90deg);
  opacity: 0;
}

.slice {
  width: 80%;
  margin: 10px auto;
}
.weeklytable {
  width: 90%;
  margin: 10px auto;
}
.title {
  position: relative;
  width: 500px;
  left: 50%;
  top: 30px;
  height: 100px;
  transform: translate(-50%);
  display: flex;
  align-items: center;
  font-size: 30px;
  text-align: center;
  margin-top: 20px;
  /* color: #ffffff; */
  color: #333;
  font-family: Arial, Helvetica, sans-serif;
}
.title .line {
