const baseurl = process.env.NODE_ENV === 'development' ? dev : prod

const bookinfo = (item) => {
  return {
    id: item.id || Math.random() * 200,
    title: item.volumeInfo.title || null,
    category: item.volumeInfo.categories || ['unknown'], // array
    image: (item.volumeInfo.imageLinks && item.volumeInfo.imageLinks.thumbnail) || null,
    page: item.volumeInfo.pageCount || 100,
    description: item.volumeInfo.description || item.volumeInfo.searchInfo && item.volumeInfo.searchInfo.textSnippet || null,
    language: item.volumeInfo.language || 'en',
    previewLink: item.volumeInfo.previewLink || null,
    infoLink: item.volumeInfo.infoLink || null,
    author: item.volumeInfo.authors || ['unknown'], // array
    rate: item.volumeInfo.averageRating || 5,
    sale: item.saleInfo && item.saleInfo.saleability || "NOT_FOR_SALE"
  }
}

const orderbook = (arr) => {
  return arr.sort((a, b) => b.rate - a.rate)
}
//get austrilia map geoJson
// @params: open data
export const ausmap = () => {
  return axios.get('https://raw.githubusercontent.com/rowanhogan/australian-states/master/states.geojson').then(v => {
    let { features } = v
    features.forEach(item => {
      item.properties.name = item.properties.STATE_NAME
    })
    return v
  })
}

