// iteration 2 google book api for guess you like
// @params: number
// @params: string
export const getHealth = (query, num) => {
  return axios.get(`https://www.googleapis.com/books/v1/volumes?q=${query}&orderBy=newest&maxResults=40&startIndex=${num}`).then(v => {
    if (num > v.totalItems - 10) {
      return 'no data'
    }
    let list = []
    let titles = []
    let index = 0
    let len = v.items.length
    let item
    for (; index < len; index++) {
      item = v.items[index]
      if (!titles.includes(item.volumeInfo.title) && list.length <= 5) {
        titles.push(item.volumeInfo.title)
        list.push(bookinfo(item))
      }
      if (list.length >= 5) {
        break;
      }
    }
    list = orderbook(list)
    list.push(index + num)
    return list
  })
}

