// spotify client ID and client secret
// const clientId = '41739662d36349d5b0f0218984a90f78'
const clientId = 'ee3ee57e13c14e4fa9541b20310acbc5'
// const clientSecret = '01fc0cbac6994733b49148932be40d10'
const clientSecret = 'fc7a5b1e474c4022b64ed957354fd8be'

// get my app spotify token
const _getToken = async () => {
  const result = await fetch('https://accounts.spotify.com/api/token', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      'Authorization': 'Basic ' + btoa(clientId + ':' + clientSecret)
    },
    body: 'grant_type=client_credentials'
  });
  const data = await result.json();
  return data.access_token;
}

// get genres list
const _getGenres = async (token) => {
  const result = await fetch(`https://api.spotify.com/v1/browse/categories?locale=sv_US`, {
    method: 'GET',
    headers: { 'Authorization': 'Bearer ' + token }
  });
  const data = await result.json();
  return data.categories.items;
}

// get genres result
const _getGr = async (term, token) => {
  const result = await fetch(`https://api.spotify.com/v1/browse/categories/${term}/playlists?locale=sv_US`, {
    method: 'GET',
    headers: { 'Authorization': 'Bearer ' + token }
  });
  const data = await result.json();
  return data.playlists.items;
}

// search by track
const _getTrack = (term, token) => {
  return axios.get(`https://api.spotify.com/v1/search?q=${term}&type=track&limit=30`, {
    headers: { 'Authorization': 'Bearer ' + token }
  }).then(v=>v.tracks.items);
}

// search by artist
const _getArt = (term, token) => {
  return axios.get(`https://api.spotify.com/v1/search?q=${term}&type=artist&limit=30`, {
    headers: { 'Authorization': 'Bearer ' + token }
  }).then(v=>v.artists.items);
}
// export object
export const spotify = {
  getToken: _getToken,
  getGenres: _getGenres,
  getGr: _getGr,
  getTrack: _getTrack,
  getArt: _getArt,
}